.home-container{
    height: 300px;
    color: white;
    margin-left: 210px;
}


.home-content h1{
    padding: 5px;
    padding-top: 100px;
    margin-left: 30px;

}



.home-content h2{
    color: #e0b50f;
    margin-left: 30px;
    font-size: 22px;
}


.home-buttons{
    margin-left: 20px;
    padding-top:20px;

}

.home-buttons .btn-hire{
    background-color: #3b8ff3;
    color: white;
    width: 160px;
}


.home-buttons .btn-hire:hover{
    background-color:white;
    color: #3b8ff3;
    width: 200px;
    transition: 1s;
}



.home-buttons .btn-cv{
    background-color: #f29f67;
    color: white;
   margin-left: 15px;
}



.home-buttons .btn-cv:hover{
   opacity: 80%;
   transition: 1s;
}


.theme-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    background-color: #1e1e1e;
    margin-top: 20px;
    text-align: center;
    position: fixed;
    right: 0;
    cursor: pointer;
    z-index: 1;

}