.navbar-profile-pic img{
    border-radius: 90px;
    height: 120px;
    width: 120px;
    display: flex;
    vertical-align: middle;
    margin: 0 auto;
}

.nav-item{
    color: white;
    margin-top: 30px;
    margin-left: 20px;
}

.nav-link{
    margin-top: 20px;
    cursor: pointer;
}

.nav-link svg{
    margin-right: 10px;
}

.nav-link :hover{
    color:#f29f67;
}

.nav-link .active{
    color: #f29f67;
    text-decoration: none;
}